<template>
  <!-- 倒计时 -->
  <div :class="className" v-if="isShow">
    <img class="clock-icon" :src="clockIcon" />
    <span v-if="time">{{ time }}</span>
    <span v-else>00:00:00</span>
  </div>
</template>

<script>
import Emitter from "@/mixins/emitter";
import Locale from "@/mixins/locale";
import { getCountDown } from "@/utils/date-toolkit.js";

const clockIcon = `data:image/png;base64,`;

export default {
  name: "count-down",
  mixins: [Emitter, Locale],
  data() {
    return {
      time: null,
      clockIcon,
      timeMeter: null //计时器
    };
  },
  props: {
    getCurrentTime: {},
    limitedTime: {
      type: Number,
      default: 0
    },
    assessmentStatus: {
      type: String,
      default: ""
    },
    mode: {
      type: String,
      default: "do"
    },
    beginTime: {
      type: Number,
      default: 0
    }
  },
  mounted() {
    this.$nextTick(() => {
      if (this.assessmentStatus === "preview" || this.mode !== "do") {
        return;
      }
      this.timer();
    });
  },
  beforeDestroy() {
    this.clearTime();
  },
  computed: {
    className: function() {
      return this.limitedTime &&
        this.assessmentStatus !== "preview" &&
        this.mode === "do"
        ? "ibs-assessment-timer ibs-assessment-timer--top"
        : "ibs-assessment-timer";
    },
    isShow: function() {
      return (this.beginTime + this.limitedTime * 60) * 1000 - Date.now() > 0;
    }
  },
  methods: {
    getContainer() {
      return document.getElementById("item-bank-sdk-card");
    },
    reachTimerSubmit() {
      this.$emit("reachTimeSubmitAnswerData");
    },
    timer() {
      const time = (this.beginTime + this.limitedTime * 60) * 1000 - Date.now();

      if (time <= 0) {
        this.arrivalTime();
        return;
      }

      // 考试倒计时
      let i = 0;
      this.timeMeter = setInterval(() => {
        let { hours, minutes, seconds } = getCountDown(time, i++);
        this.time = `${hours}:${minutes}:${seconds}`;
        if (
          (Number(hours) == 0 &&
            Number(minutes) == 0 &&
            Number(seconds) == 0) ||
          Number(seconds) < 0
        ) {
          this.arrivalTime();
        }
      }, 1000);
    },
    arrivalTime() {
      this.clearTime();
      //直接交卷
      this.reachTimerSubmit();
    },
    clearTime() {
      clearInterval(this.timeMeter);
      this.timeMeter = null;
    }
  }
};
</script>
