import Vue from "vue";
import {
  TreeSelect,
  Radio,
  Checkbox,
  LocaleProvider,
  Row,
  Col,
  Form,
  Input,
  Button,
  Collapse,
  Modal,
  Icon,
  Anchor,
  Table,
  message,
  Tooltip,
  Affix,
  Alert,
  Select,
  InputNumber,
  Dropdown,
  Menu
} from "ant-design-vue";
Vue.use(TreeSelect)
  .use(LocaleProvider)
  .use(Radio)
  .use(Checkbox)
  .use(Row)
  .use(Col)
  .use(Form)
  .use(Input)
  .use(Button)
  .use(Icon)
  .use(Modal)
  .use(Collapse)
  .use(Anchor)
  .use(Table)
  .use(Tooltip)
  .use(Affix)
  .use(Alert)
  .use(Select)
  .use(InputNumber)
  .use(Dropdown)
  .use(Menu)
  .use(message);
message.config({
  getContainer: () => {
    return document.getElementsByClassName("ibs-vue")[0] || document.body;
  }
});
Vue.prototype.$modal = Modal;
Vue.prototype.$confirm = Modal.confirm;
Vue.prototype.$warning = Modal.warning;
Vue.prototype.$message = message;
