/* eslint-disable no-mixed-operators */

// 秒转化为时间
const sec2Time = (sec, locale) => {
  let time = "";
  let h = parseInt((sec % 86400) / 3600);
  let s = parseInt((sec % 3600) / 60);
  let m = sec % 60;
  if (h > 0) {
    time += h + locale.hour;
  }
  if (s.toString().length < 2) {
    time += "0" + s + locale.minute;
  } else {
    time += s + locale.minute;
  }
  if (m.toString().length < 2) {
    time += "0" + m + locale.second;
  } else {
    time += m + locale.second;
  }
  return time;
};

const timeStampFormatTime = timeStamp => {
  const date = new Date(timeStamp * 1000);
  const year = date.getFullYear();
  const month = date.getMonth() + 1;
  const day = date.getDate();
  const hour = date.getHours();
  const minute = date.getMinutes();
  const second = date.getSeconds();
  return `${[year, month, day]
    .map(n => {
      n = n.toString();
      return n[1] ? n : `0${n}`;
    })
    .join("-")} ${[hour, minute, second]
    .map(n => {
      n = n.toString();
      return n[1] ? n : `0${n}`;
    })
    .join(":")}`;
};

// 2018/12/06 12:03
const formatTime = date => {
  const year = date.getFullYear();
  const month = date.getMonth() + 1;
  const day = date.getDate();
  const hour = date.getHours();
  const minute = date.getMinutes();
  const second = date.getSeconds();
  return `${[year, month, day]
    .map(n => {
      n = n.toString();
      return n[1] ? n : `0${n}`;
    })
    .join("/")} ${[hour, minute, second]
    .map(n => {
      n = n.toString();
      return n[1] ? n : `0${n}`;
    })
    .join(":")}`;
};

// 2018-12-06 12:03
const formatCompleteTime = date => {
  const reg = new RegExp("/", "g");
  const time = formatTime(date).replace(reg, "-");
  return time.slice(0, -3);
};

const dateTimeDown = date => {
  const now = new Date().getTime();
  if (now > date) {
    return "已到期";
  }
  const diff = parseInt((date - now) / 1000, 10);
  let day = parseInt(diff / 24 / 60 / 60, 10);
  let hour = parseInt((diff / 60 / 60) % 24, 10);
  let minute = parseInt((diff / 60) % 60, 10);
  let second = parseInt(diff % 60, 10);
  day = day ? `${day}天` : "";
  hour = day || hour ? `${hour || "0"}小时` : "";
  minute = day || hour || minute ? `${minute || "0"}分` : "";
  second = `${second || "0"}秒`;
  const time = day + hour + minute + second;
  return time;
};

// days（传时间戳）
const getOffsetDays = (time1, time2) => {
  const offsetTime = Math.abs(time1 - time2);
  return Math.floor(offsetTime / (3600 * 24 * 1e3));
};

// 将0-9的数字前面加上0，例1变为01
const checkTime = i => {
  if (i < 10 && i >= 0) {
    i = `0${i}`;
  }
  return i;
};

// 倒计时 01:01:01 传入时间戳
const getCountDown = (time, i) => {
  const nowTime = Number(time) - i * 1000;
  let minutes = parseInt((nowTime / 1000 / 60) % 60, 10); // 计算剩余的分钟
  let seconds = parseInt((nowTime / 1000) % 60, 10); // 计算剩余的秒数
  let hours = parseInt(nowTime / (1000 * 60 * 60), 10); // 计算剩余的小时
  minutes = checkTime(minutes);
  seconds = checkTime(seconds);
  hours = checkTime(hours);
  return { hours, minutes, seconds };
};

// 剩余时间 1天1小时1分钟  秒只是在最后60秒显示
const getdateTimeDown = (date, i) => {
  date = Number(date) - i * 1000;
  const now = new Date().getTime();
  if (now >= date) {
    return "";
  }
  const diff = parseInt((date - now) / 1000, 10);
  let day = parseInt(diff / 24 / 60 / 60, 10);
  let hour = parseInt((diff / 60 / 60) % 24, 10);
  let minute = parseInt((diff / 60) % 60, 10);
  let second = parseInt(diff % 60, 10);
  day = day ? `${day}天` : "";
  hour = day || hour ? `${hour || "0"}小时` : "";
  minute = day || hour || minute ? `${minute || "0"}分钟` : "";
  second = !day && !hour && !minute ? `${second || "0"}秒` : "";
  const time = day + hour + minute + second;
  return time;
};

const isMobileDevice = () => {
  return navigator.userAgent.match(/(iPhone|iPod|Android|ios|iPad)/i);
};

export {
  formatTime,
  formatCompleteTime,
  dateTimeDown,
  getOffsetDays,
  getCountDown,
  getdateTimeDown,
  sec2Time,
  timeStampFormatTime,
  isMobileDevice
};
