import itemManage from "./components/item-manage";
import itemEngine from "./components/item-engine";
import itemReview from "./components/item-review";
import itemReport from "./components/item-report";
import itemPreview from "./components/item-preview";
import itemImport from "./components/item-import";
import assessmentPreview from "./components/assessment-preview";
import attachmentUpload from "./components/attachment-upload";
import attachmentPreview from "./components/attachment-preview";
import assessmentResult from "./components/assessment-result";
import inspectionControl from "./components/inspection-control";
import sceneReport from "./components/scene-report";
import local from "./locale/index";
import "./plugins/index";

const components = [
  itemManage,
  itemEngine,
  itemReview,
  itemReport,
  itemPreview,
  assessmentPreview,
  assessmentResult,
  attachmentUpload,
  attachmentPreview,
  itemImport,
  inspectionControl,
  sceneReport
];

const install = function(Vue, options = {}) {
  console.log("options: ", options);
  if (install.installed) return;
  local.use(options.locale);
  local.i18n(options.i18n);
  components.map(component => Vue.component(component.name, component));
};

// 判断是否是直接引入文件
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}

export default {
  install,
  itemManage,
  itemEngine,
  itemReview,
  itemReport,
  itemPreview,
  assessmentPreview,
  assessmentResult,
  attachmentUpload,
  attachmentPreview,
  itemImport,
  inspectionControl,
  sceneReport
};
